export default {
  data() {
    return {
      checkbox: [{
        value: '全市',
        checked: true
      }, {
        value: '河西',
        checked: false
      }, {
        value: '郊区',
        checked: false
      }, {
        value: '零陵',
        checked: false
      }, {
        value: '祁阳',
        checked: false
      }, {
        value: '东安',
        checked: false
      }, {
        value: '道县',
        checked: false
      }, {
        value: '宁远',
        checked: false
      }, {
        value: '江华',
        checked: false
      }, {
        value: '宁永',
        checked: false
      }, {
        value: '蓝山',
        checked: false
      }, {
        value: '双牌',
        checked: false
      }, {
        value: '新田',
        checked: false
      }],
      index: -1,
      picker: ['商圈', '社区', '自营', '支局', '商客'],
      index1: -1,
      picker1: ['营业员', '渠道经理', '客户经理', '管理员'],
      date: '2019-2-25',
      finishdate: '2019-3-28',
      textareaBValue: '',
      modalName: null,
      ruleTitle: '',
      regionId: '',
      regionIdRaw: '',
      ruleType: '',
      role: '',
      ruleDatetimeStart: '',
      ruleDatetimeEnd: '',
      ruleMoney: "",
      ruleNum: "",
      ruleContent: '',
      type: false
    };
  },

  components: {
    uniPopup
  },
  methods: {
    check() {
      console.log("=========");
      this.type = true;
    },

    handleClose() {
      this.type = false;
    },

    checkSubmit() {
      this.type = false;
    },

    logPub() {
      for (var i = 0; i < this.regionIdRaw.length; i++) {
        this.regionId += this.regionIdRaw[i] + "&";
      }

      let addData = {};
      var seperator2 = ":";
      addData.ruleTitle = this.ruleTitle;
      addData.regionId = this.regionId;
      addData.ruleType = this.picker[this.index];
      addData.roleId = this.index1;
      let rulePublishDatetime = {};
      rulePublishDatetime.ruleDatetimeStart = this.date + " " + "00" + seperator2 + "00" + seperator2 + "00";
      rulePublishDatetime.ruleDatetimeEnd = this.finishdate + " " + "00" + seperator2 + "00" + seperator2 + "00";
      rulePublishDatetime.ruleMoney = this.ruleMoney;
      rulePublishDatetime.ruleNum = this.ruleNum;
      addData.rulePublishDatetime = rulePublishDatetime; //addData.ruleDatetimeStart=this.date+" " +"00" + seperator2 +  "00" +seperator2 +"00";
      //addData.ruleDatetimeEnd=this.finishdate+ " " +"00" + seperator2 +  "00" +seperator2 +"00"

      addData.ruleContent = this.ruleContent;
      console.log(addData);
      uni.request({
        url: this.$serverUrl + '/publish/insertPublish',
        data: addData,
        method: 'POST',
        success: res => {
          console.log(res);

          if (res.statusCode == 405) {
            uni.showToast({
              title: '提交失败',
              icon: 'none',
              duration: 1000
            });
          } else {
            uni.showToast({
              title: '提交成功',
              icon: 'success',
              duration: 1000
            });
          } // 							uni.navigateBack({ //提交后返回上一个页面
          // 								delta: 1,
          // 							})

        }
      });
    },

    PickerChange(e) {
      this.index = e.detail.value;
    },

    PickerChange1(d) {
      this.index1 = d.detail.value;
    },

    DateChange(e) {
      this.date = e.detail.value;
    },

    FinishDateChange(e) {
      this.finishdate = e.detail.value;
    },

    CheckboxChange(e) {
      var items = this.checkbox,
          values = e.detail.value;
      console.log(values);
      this.regionId = values;

      for (var i = 0, lenI = items.length; i < lenI; ++i) {
        items[i].checked = false;

        for (var j = 0, lenJ = values.length; j < lenJ; ++j) {
          if (items[i].value == values[j]) {
            items[i].checked = true;
            break;
          }
        }
      }
    }

  }
};